init();

//綁定id
function setDataId(){
let num = 0;
    data.forEach(function(item){
        let year = Number(item.time.split('-')[0]);
        let month = Number(item.time.split('-')[1]) - 1;
        let day = Number(item.time.split('-')[2]);

        let time = new Date(year, month ,day).getTime();
        num += .2; //避免重複
        time += num;
        item.id = time ;
    });

}

